import { find } from 'cc'
import type { Music } from '../components/audio/Music'
import type { Sound } from '../components/audio/Sound'
import AnimUtils from './AnimUtils'

const {
    Utils: { Signal: S },
} = Ludex

type SoundKeys = Ludex.Plugins.Audio.SoundKeys
type PlayConfig = Ludex.Plugins.Audio.PlayConfig

class AudioUtilsCore {
    protected static inst$: AudioUtilsCore

    protected readonly signalController$ = new S.SignalController()

    protected currentMusic$!: SoundKeys

    public static get inst() {
        if (!this.inst$) {
            this.inst$ = new this()
        }

        return this.inst$
    }

    protected get sound(): Sound | null {
        return find('Sound')?.getComponent('Sound') as Sound | null
    }

    protected get music(): Music | null {
        return find('Music')?.getComponent('Music') as Music | null
    }

    public static muteAudio(isMute: boolean): void {
        this.muteMusic(isMute)
        this.muteSound(isMute)
    }

    public static muteSound(isMute: boolean): void {
        this.inst.sound?.mute(isMute)
    }

    public static muteMusic(isMute: boolean): void {
        this.inst.music?.mute(isMute)
    }

    public static getCurrentMusic(): SoundKeys {
        return this.inst.currentMusic$
    }

    public static async turnUpMusic(): Promise<void> {
        const currentVolume = this.inst.music?.getVolume() ?? 0
        if (currentVolume > 0.6) return

        await this.setVolumeMusic(0.6, true)
    }

    public static async turnDownMusic(): Promise<void> {
        const currentVolume = this.inst.music?.getVolume() ?? 0
        if (currentVolume < 0.2) return

        await this.setVolumeMusic(0.2, true)
    }

    public static async turnOffMusic(): Promise<void> {
        await this.setVolumeMusic(0)
    }

    public static getVolumeMusic(): number {
        return this.inst.music?.getVolume() ?? 0
    }

    public static async setVolumeMusic(volume: number, withTrans = false): Promise<void> {
        if (!this.inst.music) return

        const signal = this.inst.signalController$.renew()

        if (!withTrans) {
            this.inst.music?.setVolume(volume)
            return
        }

        const currentVolume = this.inst.music.getVolume()
        let nextVolume = currentVolume

        await AnimUtils.runOnUpdateAsync(
            this.inst.music.node,
            1,
            (ratio) => {
                if (!this.inst.music) return
                nextVolume = currentVolume + (volume - currentVolume) * ratio
                this.inst.music.setVolume(nextVolume)
            },
            signal,
        )
    }
    public static async playSound(name: SoundKeys, options?: PlayConfig): Promise<void> {
        return this.inst.sound?.play(name, options)
    }

    public static pauseSound(name: SoundKeys) {
        this.inst.sound?.pause(name)
    }

    public static resumeSound(name: SoundKeys) {
        this.inst.sound?.resume(name)
    }

    public static stopSound(name: SoundKeys) {
        this.inst.sound?.stop(name)
    }

    public static stopAllSounds() {
        this.inst.sound?.stopAll()
    }

    public static playMusic(name: SoundKeys, forceRestart = false): void {
        if (!this.inst.music) return

        if (this.inst.currentMusic$ !== name) {
            this.pauseMusic()
        }

        this.inst.currentMusic$ = name

        if (!forceRestart && this.inst.music.playing(name)) {
            return
        }

        if (forceRestart) {
            this.inst.music.stop(name)
        }

        this.inst.music.play(name)
    }

    public static pauseMusic() {
        this.inst.music?.pause(this.inst.currentMusic$)
    }
}

class AudioUtils extends AudioUtilsCore {
    // * Custom logic here
}

export default AudioUtils
