import type AnimAbortSignal from './AnimAbortSignal'

const {
    Utils: { Signal: S },
} = Ludex

type Signal = InstanceType<typeof S.Signal>

export default class AnimAbortSignalAdapter implements Signal {
    protected signal: AnimAbortSignal

    protected oldCallbacks: (() => void)[] = []
    protected newCallbacks: ((error: Error) => void)[] = []

    constructor(signal: AnimAbortSignal) {
        this.signal = signal
    }

    public once(callback: (reason: Error) => void): void {
        const oldCallback = () => callback(new Error(this.signal.reason))
        this.oldCallbacks.push(oldCallback)
        this.newCallbacks.push(callback)
        this.signal.addAbortListener(oldCallback)
    }

    public off(callback: (reason: Error) => void): void {
        const index = this.newCallbacks.indexOf(callback)
        if (index === -1) return

        const oldCallback = this.oldCallbacks[index] as (typeof this.oldCallbacks)[number]
        this.signal.removeAbortListener(oldCallback)

        this.oldCallbacks.splice(index, 1)
        this.newCallbacks.splice(index, 1)
    }

    public abort(message?: string): void
    public abort(reason?: Error): void
    public abort(reasonOrMessage?: Error | string): void {
        const text = reasonOrMessage instanceof Error ? reasonOrMessage.message : reasonOrMessage
        this.signal.abort(text)
    }

    public get aborted(): boolean {
        return this.signal.aborted
    }

    public get reason(): Error | undefined {
        return typeof this.signal.reason === 'string' ? new Error(this.signal.reason) : undefined
    }

    public wrap<F extends (...args: any[]) => any>(callback: F): F | undefined {
        if (this.aborted) return undefined
        return callback
    }

    public chain(signal: Ludex.Utils.Signal.Signal | AbortSignal): void {
        if (signal instanceof S.Signal) {
            signal.once(() => this.abort())
            return
        }

        signal.addEventListener('abort', () => this.abort())
    }

    public promise(): Promise<Error> {
        if (this.aborted) {
            return Promise.resolve(this.reason as Error)
        }

        return new Promise((resolve) => {
            this.once((reason) => resolve(reason))
        })
    }
}
