import { find } from 'cc'
import { ScreenKeysEnum, ScreenNames } from '../constants/ScreenKeys'
import type { Loading } from '../screens/loading-full-screen/Loading'

const { event } = codex
const {
    Events,
    Utils: { Array: A },
} = Ludex

class ScreenUtilsCore {
    protected static inst$: ScreenUtilsCore

    protected readonly showedScreens$: string[] = []

    public static get inst() {
        if (!this.inst$) {
            this.inst$ = new this()
        }

        return this.inst$
    }

    public static preloadScreen(screenKey: ScreenKeysEnum): void {
        const screenName = ScreenNames[screenKey]
        if (!screenName) return

        event.emit(Events.PRELOAD_SCREEN, { screenName })
    }

    public static preloadScreens(): void {
        event.emit(Events.PRELOAD_SCREENS)
    }

    public static openScreen(screenKey: ScreenKeysEnum, screenData?: ScreenData): Promise<void> {
        const screenName = ScreenNames[screenKey]
        event.emit(Events.OPEN_SCREEN, { screenName, screenData })

        return new Promise((resolve) => {
            const cb = (payload: SwitchScreenPayload) => {
                if (payload.screenName !== screenName) return
                event.off(Events.OPENED_SCREEN, cb)
                resolve()
            }

            event.on(Events.OPENED_SCREEN, cb)
        })
    }

    public static closeScreen(screenKey: ScreenKeysEnum): Promise<void> {
        const screenName = ScreenNames[screenKey]
        event.emit(Events.CLOSE_SCREEN, { screenName })

        return new Promise((resolve) => {
            const cb = (payload: SwitchScreenPayload) => {
                if (payload.screenName !== screenName) return
                event.off(Events.CLOSED_SCREEN, cb)
                resolve()
            }

            event.on(Events.CLOSED_SCREEN, cb)
        })
    }

    public static async openScreenAndWaitForClose(
        screenKey: ScreenKeysEnum,
        screenData?: ScreenData,
    ): Promise<SwitchScreenPayload> {
        await this.openScreen(screenKey, screenData)

        return await new Promise((resolve) => {
            const onCloseScreen = (payload: SwitchScreenPayload) => {
                if (payload.screenName !== ScreenNames[screenKey]) return
                event.off(Events.CLOSE_SCREEN, onCloseScreen)
                resolve(payload)
            }

            event.on(Events.CLOSE_SCREEN, onCloseScreen)
        })
    }

    public static showLoadingScreen(duration?: number) {
        return this.openScreen(ScreenKeysEnum.LOADING_SCREEN, { duration })
    }

    public static closeLoadingScreen() {
        return this.closeScreen(ScreenKeysEnum.LOADING_SCREEN)
    }

    public static showNotificationScreen(message: string, duration?: number) {
        return this.openScreen(ScreenKeysEnum.NOTIFICATION_SCREEN, { message, duration })
    }

    public static showNotificationScreenWithLocalizedKey(
        localizedMessageKey: Ludex.Plugins.Language.TextKey,
        duration?: number,
    ) {
        return this.openScreen(ScreenKeysEnum.NOTIFICATION_SCREEN, { localizedMessageKey, duration })
    }

    public static closeNotificationScreen() {
        return this.closeScreen(ScreenKeysEnum.NOTIFICATION_SCREEN)
    }

    public static addShowedScene(sceneName: string) {
        if (A.has(this.inst.showedScreens$, sceneName)) return
        this.inst.showedScreens$.push(sceneName)
    }

    public static async showLoadingFullScreen(sceneName: string) {
        if (A.has(this.inst.showedScreens$, sceneName)) return
        this.addShowedScene(sceneName)

        const loadingNode = find('Loading')
        if (!loadingNode) return

        const loading = loadingNode.getComponent('Loading') as Loading
        await loading.show(sceneName)
    }

    public static async hideLoadingFullScreen(sceneName: string) {
        if (A.has(this.inst.showedScreens$, sceneName)) return
        const loading = find('Loading')?.getComponent('Loading') as Loading
        await loading?.hide(sceneName)
    }

    public static showLoadingNotificationScreen() {
        return this.openScreen(ScreenKeysEnum.NOTIFICATION_SCREEN, {
            message: 'Loading',
            enableCloseOnTouch: false,
        })
    }
}

class ScreenUtils extends ScreenUtilsCore {
    public static showTournamentDetailScreen(
        tournament: string,
        tournamentContextType?: TournamentContextType,
        playAnimation = true,
    ) {
        return this.openScreen(ScreenKeysEnum.TOURNAMENT_DETAIL_SCREEN, {
            playAnimation,
            tournament,
            tournamentContextType,
        })
    }

    public static closeTournamentDetailScreen() {
        return this.closeScreen(ScreenKeysEnum.TOURNAMENT_DETAIL_SCREEN)
    }
}

export default ScreenUtils
