import { Animation, type Node as CCNode, type Tween, tween } from 'cc'
import type AnimAbortSignal from './common/AnimAbortSignal'
import AnimAbortSignalAdapter from './common/AnimAbortSignalAdapter'

const {
    Utils: { Signal: S },
} = Ludex

type Signal = InstanceType<typeof S.Signal>

function adaptDeprecatedAbortSignal(signal: AnimAbortSignal | Signal): Signal {
    if (signal instanceof S.Signal) {
        return signal
    }

    return new AnimAbortSignalAdapter(signal)
}

export type TweenObj<T extends object> = {
    tween: Tween<T>
}

class AnimUtilsCore {
    protected static inst$: AnimUtilsCore

    public static get inst() {
        if (!this.inst$) {
            this.inst$ = new this()
        }

        return this.inst$
    }

    /**
     * Elastic ease-out.
     *
     * @function Phaser.Math.Easing.Elastic.Out
     * @since 3.0.0
     *
     * @param {number} v - The value to be tweened.
     * @param {number} [amplitude=0.1] - The amplitude of the elastic ease.
     * @param {number} [period=0.1] - Sets how tight the sine-wave is, where smaller values are tighter waves, which result in more cycles.
     *
     * @return {number} The tweened value.
     */
    public static elasticOut(v: number, amplitude: number, period: number): number {
        let a = amplitude || 0.1
        const p = period || 0.1

        if (v === 0) {
            return 0
        }

        if (v === 1) {
            return 1
        }

        let s = p / 4

        if (a < 1) {
            a = 1
        } else {
            s = (p * Math.asin(1 / a)) / (2 * Math.PI)
        }

        return a * 2 ** (-10 * v) * Math.sin(((v - s) * (2 * Math.PI)) / p) + 1
    }

    public static easeOutExpo(v: number): number {
        return v === 1 ? 1 : 1 - 2 ** (-10 * v)
    }

    public static easeInExpo(v: number): number {
        return v === 0 ? 0 : 2 ** (10 * v - 10)
    }

    public static runTweenAsync<T extends object>(
        tween: Tween<T>,
        abortSignal?: AnimAbortSignal | Signal,
    ): Promise<TweenObj<T>> {
        if (abortSignal === undefined) {
            return new Promise((resolve) => {
                tween.call(() => resolve({ tween })).start()
            })
        }

        const signal = adaptDeprecatedAbortSignal(abortSignal)

        if (signal.aborted) return Promise.resolve({ tween })

        return new Promise((resolve) => {
            const cancel = () => {
                tween.stop()
                resolve({ tween })
            }

            signal.once(cancel)

            tween
                .call(() => {
                    signal.off(cancel)
                    resolve({ tween })
                })
                .start()
        })
    }

    public static runOnUpdateAsync(
        bind: CCNode,
        duration: number,
        onUpdate: (ratio: number) => void,
        abortSignal?: AnimAbortSignal | Signal,
    ): Promise<TweenObj<CCNode>> {
        return new Promise((resolve, reject) => {
            const tw = tween(bind).to(
                duration,
                {},
                {
                    onUpdate: (_, ratio) => {
                        if (ratio === undefined) return
                        try {
                            onUpdate(ratio)
                        } catch (error) {
                            reject(error instanceof Error ? error : new Error(String(error)))
                        }
                    },
                },
            )

            if (abortSignal === undefined) {
                tw.call(() => resolve({ tween: tw })).start()
                return
            }

            const signal = adaptDeprecatedAbortSignal(abortSignal)

            if (signal.aborted) {
                resolve({ tween: tw })
                return
            }

            const cancel = () => {
                tw.stop()
                resolve({ tween: tw })
            }

            signal.once(cancel)

            tw.call(() => {
                signal.off(cancel)
                resolve({ tween: tw })
            }).start()
        })
    }

    public static waitTweenAsync(seconds: number, abortSignal?: AnimAbortSignal | Signal): Promise<void> {
        return new Promise((resolve) => {
            const tw = tween({}).delay(seconds)

            if (abortSignal === undefined) {
                tw.call(() => resolve()).start()
                return
            }

            const signal = adaptDeprecatedAbortSignal(abortSignal)

            if (signal.aborted) {
                resolve()
                return
            }

            const cancel = () => {
                tw.stop()
                resolve()
            }

            signal.once(cancel)

            tw.call(() => {
                signal.off(cancel)
                resolve()
            }).start()
        })
    }

    public static runAnimationAsync(
        animation: Animation,
        name?: string,
        abortSignal?: AnimAbortSignal | Signal,
    ): Promise<void> {
        if (abortSignal === undefined) {
            return new Promise((resolve) => {
                animation.once(Animation.EventType.FINISHED, resolve)
                animation.play(name)
            })
        }

        const signal = adaptDeprecatedAbortSignal(abortSignal)

        if (signal.aborted) return Promise.resolve()

        return new Promise((resolve) => {
            const cancel = () => {
                animation.stop()
                resolve()
            }

            signal.once(cancel)

            animation.once(Animation.EventType.FINISHED, () => {
                signal.off(cancel)
                resolve()
            })

            animation.play(name)
        })
    }
}

class AnimUtils extends AnimUtilsCore {
    // * Custom logic here
}

export default AnimUtils
