import type { Scene } from 'cc'
import SceneKeys from '../constants/SceneKeys'
import WasmUtils from './WasmUtils'

const { event } = codex
const { Events } = Ludex

class SceneUtilsCore {
    protected static inst$: SceneUtilsCore

    protected loadedScenes$: Record<string, boolean> = {}

    public static get inst() {
        if (!this.inst$) {
            this.inst$ = new this()
        }

        return this.inst$
    }

    public static switchScene(sceneName: string, sceneData?: SceneData): void {
        event.emit(Events.SWITCH_SCENE, { sceneName, sceneData })
    }

    public static preloadScene(sceneName: string): void {
        event.emit(Events.PRELOAD_SCENE, { sceneName })
    }

    public static isSceneLoaded(sceneName: string): boolean {
        return this.inst.loadedScenes$[sceneName] ?? false
    }

    public static markIsSceneLoaded(sceneName: string): void {
        this.inst.loadedScenes$[sceneName] = true
    }

    public static markIsTestScene(sceneName: string): void {
        event.emit(Events.MARK_IS_TEST_SCENE, { sceneName })
    }

    public static getSceneData(scene: Scene): SceneData {
        if (!('data' in scene.scene)) return {}

        return scene.scene.data || {}
    }
}

class SceneUtils extends SceneUtilsCore {
    public static preloadDashboardScene(): void {
        this.preloadScene(SceneKeys.DASHBOARD_SCENE)
    }

    public static switchToDashboardScene(sceneData?: SceneData): void {
        this.switchScene(SceneKeys.DASHBOARD_SCENE, sceneData)
    }

    public static async preloadGameScene(): Promise<void> {
        // ? Safely case, if game use spine, must load spine module before preload game scene
        await WasmUtils.loadModuleAsync('spine')
        this.preloadScene(SceneKeys.GAME_SCENE)
    }

    public static async switchToGameScene(sceneData?: SceneData): Promise<void> {
        // ? Safely case, if game use spine, must load spine module before switch to game scene
        await WasmUtils.loadModuleAsync('spine')
        this.switchScene(SceneKeys.GAME_SCENE, sceneData)
    }
}

export default SceneUtils
