/**
 * @example Only Portrait:
 */

const GameWorld = {
    Layout: {
        Portrait: 'portrait',
        Landscape: 'landscape',
    },

    // ! Please update design size for your game
    DesignSize: {
        Portrait: {
            WIDTH: 375 * 2,
            HEIGHT: 791.5 * 2,
        },
        Landscape: {
            WIDTH: 1366 * 2,
            HEIGHT: 768 * 2,
        },
        // ? This is resolution of assets
        // Note: Cocos Engine currently only supports a maximum resolution scale of 2
        Resolution: 2,
    },

    // * This ratio is only optimize for this game, not for all game
    // ? Target all popup in game will fit to this ratio
    AcceptPortraitRatio: 690 / 829,

    // Default is 'auto', or 'portrait' or 'landscape'
    // ? If 'auto', the game will be auto detect layout by device orientation
    PRIMARY_LAYOUT: 'auto' as 'auto' | 'portrait' | 'landscape',

    // Default is false
    // ? Canvas size will be same as screen size
    // * Canvas size is not world size
    FULL_CANVAS_TO_SCREEN: true,

    // Default is false
    // ? This mean portrait layout will be fill to full canvas
    // * Disable to keep design resolution size, only difference if FULL_CANVAS_TO_SCREEN is true
    FULL_PORTRAIT_SIZE: true,

    // Default is false
    // ? This mean landscape layout will be fill to current canvas
    // * Disable to keep design resolution size, only difference if FULL_CANVAS_TO_SCREEN is true
    FULL_LANDSCAPE_SIZE: true,
} as const

export default GameWorld
