import EnLanguageData from '../scenes/common/locales/en.json'

type Locale = Ludex.Plugins.Language.Locale

type Locales = Ludex.Plugins.Language.Locales

export const DefaultLocale = 'en' as const satisfies Locale

export const DefaultLanguageData = EnLanguageData

export type LocalesWithoutDefault = Exclude<Locale, typeof DefaultLocale>

export const AvailableLocales = [DefaultLocale, 'ru'] as const satisfies Locale[]

export const LocaleFontPaths = {
    en: 'fonts/mikado-bold.ttf',
    ru: 'fonts/nunito-black.ttf',
} as const satisfies Record<Locale, string>

export const LocaleFilePaths = {
    ru: 'locales/ru',
} as const satisfies Record<LocalesWithoutDefault, string>

export const EditorLocaleLoader = {
    ru: () => import('db://assets/loadable/locales/ru.json').then((m) => m.default),
} as const satisfies Record<LocalesWithoutDefault, () => Promise<Locales[LocalesWithoutDefault]>>

export const LocaleFrameCaptureFontSize = {
    en: '104px',
    ru: '96px',
} as const satisfies Record<Locale, string>
