import { view } from 'cc'
import Ads from '../constants/Ads'
import GameWorld from '../constants/GameWorld'
import AudioUtils from './AudioUtils'
import CocosUtils from './CocosUtils'
import ScreenUtils from './ScreenUtils'

const {
    Events,
    Configs: {
        Ads: { Enabled },
        Gameplay: { StartAdAtLevel },
    },
    Plugins: { Ads: { Types: AdsTypes, Status: AdStatus } = {} },
    Utils: { Array: A, Function: F, Object: O },
} = Ludex

const { ads, event, player } = codex

type AdTypes = Ludex.Plugins.Ads.Types
type NormalAdTypes = Ludex.Plugins.Ads.NormalAdTypes

type AdServiceName = Ludex.Configs.AdServiceName
type BannerAdOption = Ludex.Configs.BannerAdOption

type Layout = (typeof GameWorld.Layout)[keyof typeof GameWorld.Layout]

type CommonAdOptions = {
    autoLoad?: boolean
    forceLoad?: boolean
    includeSDK?: SDKNames[]
    excludeSDK?: SDKNames[]
    adService?: AdServiceName
    placementId?: string
    showError?: boolean
    timeout?: number
}

type ShowInterAdOptions = CommonAdOptions & {
    forLevel?: number
    contextType?: AdInterstitialType
}

type ShowRewardedAdOptions = CommonAdOptions & {
    contextType?: AdRewardType
}

type ShowRewardedInterstitialAdOptions = CommonAdOptions & {
    contextType?: AdRewardType
}

class LoadAdTimeoutError extends Error {
    constructor(duration: number) {
        super(`AdsUtils.loadAdAsync: Timed out after ${duration}ms`)
    }
}

class AdsUtilsCore {
    protected static inst$: AdsUtilsCore

    protected adsEnable$: Record<string, boolean> = {}

    protected visibleBannerConfigs$: BannerAdOption[] = []
    protected invisibleBannerConfigs$: BannerAdOption[] = []

    public static get inst() {
        if (!this.inst$) {
            this.inst$ = new this()
        }

        return this.inst$
    }

    public static setAdEnable(type: AdTypes, enable: boolean) {
        this.inst.adsEnable$[type] = enable
    }

    public static isAdEnabled(type: AdTypes): boolean {
        if (!Enabled) return false
        return this.inst.adsEnable$[type] !== false
    }

    public static async preloadAds() {
        if (!AdsTypes) return

        this.preloadAdsByType(AdsTypes.INTERSTITIAL)
        this.preloadAdsByType(AdsTypes.REWARDED_VIDEO)
        this.preloadAdsByType(AdsTypes.REWARDED_INTERSTITIAL)
    }

    public static isServiceDegraded(): boolean {
        return ads.isServiceDegraded()
    }

    // * This will preload all ads by type, including ad services
    public static preloadAdsByType(type: NormalAdTypes, placementId?: string) {
        if (!this.isAdEnabled(type) || !AdStatus) return

        const adsList = ads.getAdsByType(type)
        const targetAds = placementId ? adsList.filter((ad) => ad.placementId === placementId) : adsList

        targetAds.forEach((ad) => {
            const status = ads.getAdStatus(type, ad.placementId)
            if (status === AdStatus.FILLED) return

            ads.loadAdAsync(type, ad.placementId).catch(() => {
                // Safe to ignore
            })
        })
    }

    public static updateBannerAdsByLayout(layout: Layout): void {
        this.inst.updateBannerAdsConfigByLayout$(layout)
    }

    protected updateBannerAdsConfigByLayout$ = (layout: Layout): void => {
        if (!AdsTypes) return

        const worldGameSize = view.getVisibleSize()
        const bannerRemainSizeCss = CocosUtils.worldSizeToCssSize(worldGameSize.height * Ads.MAX_BOTTOM_BANNER_RATIO)
        const isLandscape = layout === GameWorld.Layout.Landscape

        const bannerAds = ads.getAdsByType(AdsTypes.BANNER)
        const validBanners = bannerAds
            .map((ad) => ad.config as BannerAdOption)
            .filter((config) => {
                const { Position, BannerHeight } = config
                return !(Position === 'bottom' && BannerHeight > bannerRemainSizeCss)
            })

        this.visibleBannerConfigs$ = validBanners.filter((config) => {
            const { Position } = config
            return isLandscape || Position === 'bottom' || Position === 'top'
        })

        this.invisibleBannerConfigs$ = validBanners.filter((config) => !this.visibleBannerConfigs$.includes(config))
    }

    public static refreshBannerAdVisibility(): void {
        if (!AdsTypes || !AdStatus) return

        if (!this.isAdEnabled(AdsTypes.BANNER)) {
            this.hideBannerAds()
            return
        }

        for (const banner of this.inst.invisibleBannerConfigs$) {
            const { PlacementId } = banner
            const ad = ads.getAdStatus(AdsTypes.BANNER, PlacementId)
            if (ad === AdStatus.SHOWING) {
                this.hideBannerAd(PlacementId)
            }
        }

        for (const banner of this.inst.visibleBannerConfigs$) {
            const { PlacementId } = banner
            const ad = ads.getAdStatus(AdsTypes.BANNER, PlacementId)
            if (ad !== false && ad !== AdStatus.SHOWING) {
                this.showBannerAd(PlacementId)
            }
        }
    }

    public static getBannerAdHeight(): number {
        try {
            if (!AdsTypes) return 0
            const gameZoom = CocosUtils.cssSizeToWorldSize(1)
            const ad = ads.getAd(AdsTypes.BANNER)
            return Math.round(ads.getBannerHeight(ad.placementId, gameZoom))
        } catch (error) {
            console.warn('[AdsUtils] getBannerAdHeight error:', error)
            return 0
        }
    }

    public static showBannerAds(): void {
        if (!AdsTypes || !AdStatus) return
        if (!this.isAdEnabled(AdsTypes.BANNER)) return

        const adsList = ads.getAdsByType(AdsTypes.BANNER)

        for (const ad of adsList) {
            const status = ads.getAdStatus(AdsTypes.BANNER, ad.placementId)
            if (status === AdStatus.IDLE) continue

            this.showBannerAd(ad.placementId)
        }
    }

    public static hideBannerAds(): void {
        if (!Enabled || !AdsTypes || !AdStatus) return

        const adsList = ads.getAdsByType(AdsTypes.BANNER)

        for (const ad of adsList) {
            const status = ads.getAdStatus(AdsTypes.BANNER, ad.placementId)
            if (status === AdStatus.IDLE) continue

            this.hideBannerAd(ad.placementId)
        }
    }

    public static showBannerAd(placementId: string): void {
        if (!AdsTypes || !this.isAdEnabled(AdsTypes.BANNER)) return

        ads.showBannerAdAsync(placementId).catch(() => {
            // Safe to ignore
        })
    }

    public static hideBannerAd(placementId: string): void {
        if (!Enabled) return
        ads.hideBannerAdAsync(placementId).catch(() => {
            // Safe to ignore
        })
    }

    protected getAdReady$ = (type: AdTypes, placementId?: string): string | null => {
        if (!AdStatus || !AdsUtils.isAdEnabled(type)) return null

        if (placementId) {
            return ads.getAdStatus(type, placementId) === AdStatus.FILLED ? placementId : null
        }

        const readyAd = ads.getAdsByType(type).find((ad) => ads.getAdStatus(type, ad.placementId) === AdStatus.FILLED)
        return readyAd?.placementId ?? null
    }

    protected getPlacementIdByService$ = (type: AdTypes, service?: AdServiceName): string | false => {
        if (!service) return false
        if (!AdsTypes) return false

        const adsList = ads.getAdsByType(type)
        const ad = A.search(adsList, (ad) => {
            const { AdService } = ad.config
            return AdService === service
        })

        return ad?.placementId ?? false
    }

    protected getAdIdByPayload$ = (type: AdTypes, payload?: CommonAdOptions): string | undefined => {
        const { adService, placementId } = payload || {}
        return this.getPlacementIdByService$(type, adService) || placementId
    }

    // * Utility-specific logic. For advanced ad handling, refer to the Ads plugin.
    public static isReadyAdShowable(type: NormalAdTypes, placementId?: string): boolean {
        const adId = this.inst.getAdReady$(type, placementId)
        if (!adId) return false

        return ads.canShowAd(type, adId)
    }

    protected getAdErrorMessage$ = (type: AdTypes, error: unknown): Ludex.Plugins.Language.TextKey | null => {
        if (!AdsTypes) return null

        switch (type) {
            case AdsTypes.REWARDED_VIDEO:
            case AdsTypes.REWARDED_INTERSTITIAL:
                if (!this.isUserCancelledAdError$(error)) {
                    return 'error.noAd'
                }
                return 'ads.cancelled'
            default:
                return null
        }
    }

    protected showAdByType$ = async (type: NormalAdTypes, payload?: CommonAdOptions): Promise<boolean> => {
        if (!AdsTypes || !AdStatus) return false
        if (!AdsUtils.isAdEnabled(type)) return false

        const {
            placementId,
            includeSDK,
            excludeSDK,
            forceLoad,
            autoLoad,
            adService,
            timeout = 15000,
            showError,
        } = payload || {}

        if (!this.canShowInSDK$(includeSDK, excludeSDK)) return false

        let adId = this.getAdIdByPayload$(type, payload)
        let isSuccess = false

        // ? Flag for preload all ads by type if show failed
        let isHasShowFailed = false

        try {
            AudioUtils.muteAudio(true)

            const status = ads.getAdStatus(type, placementId)
            const shouldLoad = forceLoad || (autoLoad && status !== AdStatus.FILLED)

            if (shouldLoad) {
                ScreenUtils.showLoadingScreen()
                await F.timeOut(ads.loadAdAsync(type, adId), timeout)
            }

            while (!isSuccess) {
                try {
                    const adIdReady = this.getAdReady$(type, adId)

                    // ! Must return to cancel while loop
                    if (!adIdReady) return false

                    adId = adIdReady

                    await ads.showAdAsync(type, adId)

                    isSuccess = true
                } catch (error) {
                    // ? If adService has set, throw error to avoid showing multiple ads
                    if (adService) throw error

                    // ? Don't show fallback ad if user cancelled
                    if (this.isUserCancelledAdError$(error)) throw error

                    // ? Don't show fallback ad when request ad timeout
                    if (this.isRequestAdTimeoutError$(error)) throw error

                    adId = undefined
                    isSuccess = false
                    isHasShowFailed = true
                }
            }

            return true
        } catch (error) {
            isHasShowFailed = true

            const errorMessage = this.getAdErrorMessage$(type, error)
            if (errorMessage && showError) {
                ScreenUtils.showNotificationScreenWithLocalizedKey(errorMessage, 2)
            }

            return false
        } finally {
            AudioUtils.muteAudio(false)

            if (forceLoad || autoLoad) {
                ScreenUtils.closeLoadingScreen()
            }

            if (isHasShowFailed) {
                // ? Avoid preload only one ad by show failed
                adId = placementId
            }

            // ? If adId is not null, will be preload all ads by type
            AdsUtils.preloadAdsByType(type, adId)

            // ? Update game size if resizing when show ad
            event.emit(Events.REQUEST_RESIZE_GAME)
        }
    }

    protected canShowInSDK$ = (includeSDK: SDKNames[] = [], excludeSDK: SDKNames[] = []): boolean => {
        const sdkName = GameSDK.getSDKName()

        if (excludeSDK?.length && A.has(excludeSDK, sdkName)) return false
        if (includeSDK?.length && !A.has(includeSDK, sdkName)) return false
        return true
    }

    protected isUserCancelledAdError$ = (error: unknown): boolean => {
        return O.hasOwn(error, 'code') && error.code === 'USER_INPUT'
    }

    protected isRequestAdTimeoutError$ = (error: unknown): boolean => {
        if (error instanceof LoadAdTimeoutError) return true
        if (O.hasOwn(error, 'code') && error.code === 'SHOW_ADS_TIMEOUT') return true
        return false
    }

    public static isBannerAdShowing(): boolean {
        if (!AdsTypes || !AdStatus) return false

        const adsList = ads.getAdsByType(AdsTypes.BANNER)

        for (const ad of adsList) {
            const currentAdStatus = ads.getAdStatus(AdsTypes.BANNER, ad.placementId)
            if (currentAdStatus === AdStatus.SHOWING) return true
        }

        return false
    }
    public static async showInterstitialAdAsync(payload?: ShowInterAdOptions): Promise<boolean> {
        if (!AdsTypes) return false

        const { forLevel, contextType, forceLoad } = payload ?? {}
        if (forLevel && StartAdAtLevel >= forLevel) return false

        if (contextType) {
            window.GoogleAds?.setInterstitialType(contextType)
            window.TranssionH5Ads?.setInterstitialType(contextType)
        }

        // ? MsGames has a delay when showing ad, so we need to show loading screen
        const isMsGames = GameSDK.isGameSDK('MsGames')

        return this.inst.showAdByType$(AdsTypes.INTERSTITIAL, {
            ...payload,
            forceLoad: forceLoad || isMsGames,
        })
    }

    public static async showRewardVideoAdAsync(payload?: ShowRewardedAdOptions): Promise<boolean> {
        if (!AdsTypes) return false

        const { contextType } = payload ?? {}

        if (contextType) {
            window.GoogleAds?.setRewardType(contextType)
            window.TranssionH5Ads?.setRewardType(contextType)
        }

        return this.inst.showAdByType$(AdsTypes.REWARDED_VIDEO, {
            showError: true,
            ...payload,
            autoLoad: true,
        })
    }

    public static async showRewardedInterstitialAdAsync(payload?: ShowRewardedInterstitialAdOptions): Promise<boolean> {
        if (!AdsTypes) return false

        const { contextType } = payload ?? {}

        if (contextType) {
            window.GoogleAds?.setRewardType(contextType)
            window.TranssionH5Ads?.setRewardType(contextType)
        }

        return this.inst.showAdByType$(AdsTypes.REWARDED_INTERSTITIAL, {
            showError: true,
            ...payload,
            autoLoad: true,
        })
    }
}

class AdsUtils extends AdsUtilsCore {
    // * Demo custom utils
    public static async preloadAds() {
        if (!AdsTypes) return

        this.preloadAdsByType(AdsTypes.INTERSTITIAL)
        this.preloadAdsByType(AdsTypes.REWARDED_VIDEO)
        // this.preloadAdsByType(AdsTypes.REWARDED_INTERSTITIAL)
    }

    public static applyRemoveAds(): void {
        if (!AdsTypes) return

        const enabled = !this.isRemoveAdActive()
        this.setAdEnable(AdsTypes.BANNER, enabled)
        this.setAdEnable(AdsTypes.INTERSTITIAL, enabled)

        this.refreshBannerAdVisibility()
    }

    public static isRemoveAdActive(): boolean {
        const { removeAdEndTime } = player.getGameData() || {}
        if (!removeAdEndTime) return false

        return removeAdEndTime > Date.now()
    }
}

export default AdsUtils
