import { game } from 'cc'
import GameEvents from '../constants/GameEvents'
import GameWorld from '../constants/GameWorld'
import CocosUtils from './CocosUtils'

const { Layout } = GameWorld
type Layout = (typeof Layout)[keyof typeof Layout]

class LayoutUtilsCore {
    protected static inst$: LayoutUtilsCore

    protected isListening$: boolean = false
    protected currentLayout$: Layout = Layout.Portrait

    public static get inst() {
        if (!this.inst$) {
            this.inst$ = new this()
        }

        return this.inst$
    }

    public static listenLayoutChanges() {
        if (this.inst.isListening$) return
        this.inst.isListening$ = true

        // @ts-expect-error - This event is custom event
        game.on(GameEvents.LAYOUT_CHANGED, this.inst.onLayoutChanged$)
    }

    public static getCurrentLayout() {
        return this.inst.currentLayout$
    }

    public static isPortrait(): boolean {
        return this.inst.currentLayout$ === Layout.Portrait
    }

    public static isLandscape(): boolean {
        return this.inst.currentLayout$ === Layout.Landscape
    }

    protected readonly onLayoutChanged$ = (layout: Layout) => {
        if (this.currentLayout$ === layout) return
        this.currentLayout$ = layout
        game.emit(GameEvents.ORIENTATION_CHANGED, layout)
    }

    public static getScreenRatio(): number {
        const screenWidth = CocosUtils.getScreenWidthCss()
        const screenHeight = CocosUtils.getScreenHeightCss()

        return screenWidth / screenHeight
    }
}

class LayoutUtils extends LayoutUtilsCore {
    // * Custom logic here
}

export default LayoutUtils
