import { director } from 'cc'
import { IgnoreAnalyticsScreenKeys } from '../constants/ScreenKeys'

const {
    Utils: { Array: A },
    Configs: { Analytics },
} = Ludex

const { analytics } = codex

class AnalyticsUtilsCore {
    protected static inst$: AnalyticsUtilsCore

    public static get inst() {
        if (!this.inst$) {
            this.inst$ = new this()
        }

        return this.inst$
    }

    protected static trackingPages$: string[] = []

    public static trackCurrentPage(pageName: string, isOpen: boolean) {
        if (!Analytics.Enabled) return

        if (A.has(IgnoreAnalyticsScreenKeys, pageName)) return

        if (!isOpen && !A.has(this.trackingPages$, pageName))
            // ? If page is not open and not in tracking list, ignore
            return

        // ? If open new page and clear this page first
        this.trackingPages$ = this.trackingPages$.filter((page) => page !== pageName)

        if (isOpen) {
            this.trackingPages$.push(pageName)
        }

        const currentPage = this.getCurrentPage()
        analytics.pageview(currentPage)
    }

    public static switchSceneTracking() {
        if (!Analytics.Enabled) return

        this.trackingPages$ = []

        const currentPage = this.getCurrentPage()
        analytics.pageview(currentPage)
    }

    public static getCurrentPage(): string {
        if (this.trackingPages$.length > 0) {
            return this.trackingPages$[this.trackingPages$.length - 1] || 'Unknown Page'
        }

        return director.getScene()?.name ?? 'Unknown Scene'
    }

    public static trackDeviceInfo(deviceInfo: Record<string, unknown>) {
        if (!Analytics.Enabled) return

        analytics.event('device_info', deviceInfo)
    }
}

class AnalyticsUtils extends AnalyticsUtilsCore {
    // * Custom logic here
}

export default AnalyticsUtils
