import {
    type Asset,
    type AssetManager,
    AudioClip,
    assetManager,
    type Constructor,
    Font,
    JsonAsset,
    Prefab,
    SpriteFrame,
} from 'cc'
import { EDITOR_NOT_IN_PREVIEW } from 'cc/env'
import { LocaleFontPaths } from '../constants/Locales'
import ResourcePaths from '../gameplay/constants/ResourcePaths'
import ThemeResourcesMapping, {
    ThemePreviewResourcesMapping,
    ThemeThumbnailResourcesMapping,
    ThemeTournamentResourcesMapping,
} from '../gameplay/constants/ThemeResourcesPath'

const {
    Utils: { Function: F },
} = Ludex

type Locale = Ludex.Plugins.Language.Locale
type Bundle = 'loadable'

class ResourceUtilsCore {
    protected static inst$: ResourceUtilsCore

    protected mutex$ = F.mutex()

    protected bundles$: Record<Bundle, AssetManager.Bundle | null> = {
        loadable: null,
    }

    public static get inst() {
        if (!this.inst$) {
            this.inst$ = new this()
        }
        return this.inst$
    }

    public static async loadBundle(name: Bundle): Promise<boolean> {
        if (EDITOR_NOT_IN_PREVIEW) return true
        if (this.inst.bundles$[name]) return true
        if (assetManager.bundles.get(name)) return true

        const lock = await this.inst.mutex$.use(`bundle-${name}`)

        if (this.inst.bundles$[name]) {
            lock.release()
            return true
        }

        const promise = new Promise<boolean>((resolve, reject) => {
            // * This means timeout canceled
            const timeoutMs = 8000
            const timeout = setTimeout(() => reject(`load bundle timeout (${timeoutMs}ms): ${name}`), timeoutMs)

            assetManager.loadBundle(name, (err, bundle) => {
                clearTimeout(timeout)
                const bundleData = this.inst.bundles$[name]

                if (!bundleData && (err || !bundle)) {
                    reject(err)
                    return
                }

                this.inst.bundles$[name] = bundleData ?? bundle

                resolve(true)
            })
        })

        const [_, err] = await F.handleAsync(promise)

        lock.release()

        return !err
    }

    protected getBundleNameByType(type?: Constructor<Asset>): Bundle {
        switch (type) {
            // * Add support for other bundles here
            default:
                return 'loadable'
        }
    }

    protected async loadBundleByType(type?: Constructor<Asset>): Promise<boolean> {
        const bundleName = this.getBundleNameByType(type)
        return ResourceUtilsCore.loadBundle(bundleName)
    }

    protected getBundleByType(type?: Constructor<Asset>): AssetManager.Bundle | null {
        const bundleName = this.getBundleNameByType(type)
        return this.bundles$[bundleName] ?? null
    }

    public static preloadSpriteFramesDir(folderPath: string, alsoLoadAsset = true): void {
        this.inst.preloadDirAsync<SpriteFrame>(folderPath, SpriteFrame, alsoLoadAsset)
    }

    protected async preloadAsset<T extends Asset>(
        path: string,
        type: Constructor<T>,
        alsoLoadAsset: boolean,
    ): Promise<void> {
        await this.loadBundleByType(type)

        const bundle = this.getBundleByType(type)
        if (!bundle) return

        // * Preload only fetch the asset info
        return new Promise((resolve, reject) => {
            bundle.preload(path, type, (err) => {
                if (err) {
                    console.warn(`Failed to preload asset at path: ${path}`, err)
                    reject(err)
                    return
                }

                // ? Preload asset data by loadAssetAsync
                if (alsoLoadAsset) {
                    this.loadAssetAsync(path, type).finally(resolve)
                } else {
                    resolve()
                }
            })
        })
    }

    protected async loadAssetAsync<T extends Asset>(path: string, type?: Constructor<T>): Promise<T | null> {
        await this.loadBundleByType(type)

        const bundle = this.getBundleByType(type)
        if (!bundle) return null

        const asset = ResourceUtilsCore.getAsset(path, type)
        if (asset) return asset

        return new Promise((resolve, reject) => {
            const output = (err: Error | null, asset: T) => {
                if (asset) {
                    resolve(asset)
                } else {
                    console.warn(`[ResourceUtils] Failed to load asset at path: ${path}`, err)
                    reject(null)
                }
            }

            if (type) {
                bundle.load(path, type, output)
            } else {
                bundle.load(path, output)
            }
        })
    }

    protected async loadAssetsAsync<T extends Asset>(paths: string[], type: Constructor<T>): Promise<T[] | null> {
        await this.loadBundleByType(type)

        const bundle = this.getBundleByType(type)
        if (!bundle) return null

        const existing: {
            index: number
            asset: T
        }[] = []

        const missing: {
            index: number
            path: string
        }[] = []

        for (let index = 0, len = paths.length; index < len; index++) {
            const path = paths[index]
            if (!path) {
                missing.push({ index, path: '' })
                continue
            }

            const asset = ResourceUtilsCore.getAsset(path, type)
            if (asset) {
                existing.push({ index, asset })
            } else {
                missing.push({ index, path })
            }
        }

        if (missing.length === 0) {
            return Promise.resolve(existing.map(({ asset }) => asset))
        }

        return new Promise((resolve) => {
            bundle.load(
                missing.map(({ path }) => path),
                type,
                (err, assets) => {
                    if (err || assets.length !== missing.length) {
                        resolve(null)
                        return
                    }

                    for (let index = 0, len = assets.length; index < len; index++) {
                        const asset = assets[index]
                        if (!asset) continue

                        const correctIndex = missing[index]?.index
                        if (!correctIndex) return

                        existing.splice(correctIndex, 0, { index: correctIndex, asset: asset })
                    }

                    resolve(existing.map(({ asset }) => asset))
                },
            )
        })
    }

    protected async loadDirAsync<T extends Asset>(folderPath: string, type: Constructor<T>): Promise<T[]> {
        await this.loadBundleByType(type)

        const bundle = this.getBundleByType(type)
        if (!bundle) return Promise.resolve([])

        return new Promise((resolve) => {
            bundle.loadDir(folderPath, type, (err: Error | null, assets: T[]) => {
                if (assets) {
                    resolve(assets)
                } else {
                    console.warn(`Failed to load assets at path: ${folderPath}`, err)
                    resolve([])
                }
            })
        })
    }

    protected async preloadDirAsync<T extends Asset>(
        folderPath: string,
        type: Constructor<T>,
        alsoLoadAsset: boolean,
    ): Promise<void> {
        await this.loadBundleByType(type)

        const bundle = this.getBundleByType(type)
        if (!bundle) return

        // * Preload only fetch the asset info
        return new Promise((resolve, reject) => {
            bundle.preloadDir(folderPath, type, (err) => {
                if (err) {
                    console.warn(`Failed to preload assets at path: ${folderPath}`, err)
                    reject(err)
                    return
                }

                if (alsoLoadAsset) {
                    // ? Preload asset data by loadDirAsync
                    this.loadDirAsync(folderPath, type).finally(resolve)
                } else {
                    resolve()
                }
            })
        })
    }

    public static getAssetInfo<T extends Asset>(path: string, type?: Constructor<T>) {
        const bundle = this.inst.getBundleByType(type)
        if (!bundle) return null

        return bundle.getInfoWithPath(path, type)
    }

    public static getAsset<T extends Asset>(path: string, type?: Constructor<T>) {
        const bundle = this.inst.getBundleByType(type)
        if (!bundle) return null

        const info = this.getAssetInfo(path, type)
        if (!info) return null

        return bundle.get(info.path, type)
    }

    public static loadAudioAsync(path: string): Promise<AudioClip | null> {
        return this.inst.loadAssetAsync(path, AudioClip)
    }

    public static loadJsonAsync(path: string): Promise<JsonAsset | null> {
        return this.inst.loadAssetAsync(path, JsonAsset)
    }

    public static loadPrefabAsync(path: string): Promise<Prefab | null> {
        return this.inst.loadAssetAsync(path, Prefab)
    }

    public static loadFontAsync(key: Locale): Promise<Font | null> {
        const path = LocaleFontPaths[key]
        const pathWithoutExt = path.replace(/\.[^.]+$/, '')
        return this.inst.loadAssetAsync(pathWithoutExt, Font)
    }

    public static async loadSpriteFrameAsync(path: string, packable = true): Promise<SpriteFrame | null> {
        const spriteFrame = await this.inst.loadAssetAsync<SpriteFrame>(path, SpriteFrame)
        if (spriteFrame) {
            spriteFrame.packable = packable
        }
        return spriteFrame
    }

    public static async loadSpriteFramesAsync(paths: string[], packable = true): Promise<SpriteFrame[] | null> {
        const assets = await this.inst.loadAssetsAsync(paths, SpriteFrame)

        if (assets) {
            for (const asset of assets) {
                asset.packable = packable
            }
        }

        return assets
    }

    public static loadSpriteFramesDirAsync(folderPath: string): Promise<SpriteFrame[]> {
        return this.inst.loadDirAsync<SpriteFrame>(folderPath, SpriteFrame)
    }
}

class ResourceUtils extends ResourceUtilsCore {
    public static async loadThemeAsync(themeId: ThemeKeys) {
        return this.loadSpriteFrameAsync(`${ThemeResourcesMapping[themeId]}/spriteFrame`, false)
    }

    public static async loadThemePreviewAsync(themeId: ThemeKeys) {
        return this.loadSpriteFrameAsync(`${ThemePreviewResourcesMapping[themeId]}/spriteFrame`)
    }

    public static async loadThemeTournamentAsync(themeId: ThemeKeys) {
        return this.loadSpriteFrameAsync(`${ThemeTournamentResourcesMapping[themeId]}/spriteFrame`)
    }

    public static async loadThemeThumbnailAsync(themeId: ThemeKeys) {
        return this.loadSpriteFrameAsync(`${ThemeThumbnailResourcesMapping[themeId]}/spriteFrame`)
    }

    public static async loadSkinPreviewAsync(skinId: SkinKeys) {
        const numOfPreview = 4
        const promises = []
        for (let i = 1; i <= numOfPreview; i++) {
            const path = `${ResourcePaths.TILE_SKIN_PREVIEW_FOLDER}/${skinId.replace(/-/g, '_')}_${i}/spriteFrame`
            promises.push(this.loadSpriteFrameAsync(path))
        }
        return Promise.all(promises)
    }
}

export default ResourceUtils
