import { find } from 'cc'

class GeneralUtilsCore {
    protected static inst$: GeneralUtilsCore

    public static get inst() {
        if (!this.inst$) {
            this.inst$ = new this()
        }
        return this.inst$
    }

    public static setBlockInput(block: boolean): void {
        const inputBlocker = find('Canvas/InputBlocker')
        if (!inputBlocker) {
            return
        }
        inputBlocker.active = block
    }

    public static getBlockInput(): boolean {
        const inputBlocker = find('Canvas/InputBlocker')
        if (!inputBlocker) {
            return false
        }
        return inputBlocker.active
    }
}

class GeneralUtils extends GeneralUtilsCore {
    // * Custom logic here
}

export default GeneralUtils
