import ThemeKeys from '../gameplay/constants/ThemeKeys'

type TournamentPayload = Ludex.Plugins.Match.TournamentPayload
type LeaderboardResponse = Ludex.Plugins.Leaderboards.Response.Leaderboard
type Locale = Ludex.Plugins.Language.Locale

const {
    Utils: { String: S, Valid: V, Array: A },
    Configs: {
        Mockup: { Leaderboards: LeaderboardsMockupConfig },
    },
} = Ludex

const { language } = codex

interface GameSDKTournamentPayload {
    [key: string]: unknown
}

interface GlobalTournamentLBPayload {
    tournamentId: string
    contextId: string
    themeId?: ThemeKeys
    i18n?: Partial<Record<Locale, TournamentI18n>>
    restrict?: boolean
}

interface TournamentI18n {
    title: string
}

export interface TournamentData {
    id: string
    title: string
    endTime: number
    contextId: string
    payload: TournamentPayload
    tournamentType: GameSDK.TournamentType
    restrict?: boolean
}

const { leaderboard, player } = codex

class TournamentUtilsCore {
    protected static inst$: TournamentUtilsCore

    protected globalTournamentLBData$: LeaderboardResponse[] = []
    protected playerTournaments$: GameSDK.Tournament[] = []

    protected tournamentData$: TournamentData[] = []

    protected maxTimeRefetch$ = 5000
    protected lastFetchTournamentsData$ = 0

    protected lastFetchGlobalTournament$ = 0

    public static get inst() {
        if (!this.inst$) {
            this.inst$ = new this()
        }

        return this.inst$
    }

    protected fetchGlobalTournamentLeaderboardAsync$ = async (): Promise<LeaderboardResponse[]> => {
        try {
            const now = Date.now()

            if (now - this.lastFetchGlobalTournament$ > this.maxTimeRefetch$) {
                this.globalTournamentLBData$ = await leaderboard.requestLeaderboardsAsync({
                    limit: 100,
                    type: 'world_tournament_leaderboard',
                })
                this.globalTournamentLBData$.sort((a, b) => {
                    const aEndTime = a.expireTime
                    const bEndTime = b.expireTime

                    //Sort ascending end time
                    return aEndTime - bEndTime
                })
            }
        } catch (error) {
            console.warn('fetchGlobalTournamentLeaderboardAsync', error)
        }

        return this.globalTournamentLBData$
    }

    protected fetchTournamentsAsync$ = async (): Promise<GameSDK.Tournament[]> => {
        try {
            const now = Date.now()
            if (now - this.lastFetchTournamentsData$ > this.maxTimeRefetch$) {
                this.playerTournaments$ = await GameSDK.tournament.getTournamentsAsync()

                this.playerTournaments$.sort((a, b) => {
                    const aEndTime = a.getEndTime()
                    const bEndTime = b.getEndTime()

                    //Sort ascending end time
                    return aEndTime - bEndTime
                })
            }
        } catch (error) {
            console.warn('fetchTournamentsAsync', error)
        }

        return this.playerTournaments$
    }

    // ? Return all tournament data without fetching
    public static getAllTournamentData(): TournamentData[] {
        const instance = this.inst
        return instance.tournamentData$
    }

    public static getTournamentDataById(id: string): TournamentData | undefined {
        const instance = this.inst
        return A.search(instance.tournamentData$, (tournament) => tournament.id === id)
    }

    public static getPlayerTournamentData(): TournamentData[] {
        const instance = this.inst
        const playerId = player.getPlayerId()

        return instance.tournamentData$.filter((tournament) => {
            const { payload } = tournament
            return payload.playerId === playerId
        })
    }

    public static getFriendsTournamentData(): TournamentData[] {
        const instance = this.inst
        const playerId = player.getPlayerId()

        return instance.tournamentData$.filter((tournament) => {
            const { payload } = tournament
            return payload.playerId !== playerId && payload.playerId !== 'global'
        })
    }

    public static getGlobalTournamentData(): TournamentData[] {
        const instance = this.inst
        return instance.tournamentData$.filter((tournament) => {
            const { payload } = tournament
            return payload.playerId === 'global'
        })
    }

    public static validateTournamentExpired(tournamentId: string): boolean {
        const tournament = this.getTournamentDataById(tournamentId)

        if (!tournament) return false

        const { endTime } = tournament
        const isExpired = endTime < Date.now() / 1000

        return isExpired
    }

    protected validateGlobalTournamentPayload(payload: GlobalTournamentLBPayload): boolean {
        const { tournamentId, contextId } = payload

        if (!V.isString(tournamentId) || tournamentId === '') {
            return false
        }

        if (!V.isString(contextId) || contextId === '') {
            return false
        }

        return true
    }

    protected getTournamentEndTime(data: LeaderboardResponse): number {
        const createTime = new Date(data.createdAt).getTime()
        const endTime = Math.round((data.expireTime * 1000 + createTime) / 1000)

        return endTime
    }

    protected validateExpireTime(data: LeaderboardResponse): boolean {
        const endTime = this.getTournamentEndTime(data)
        const remainingTimeInMs = endTime * 1000 - Date.now()

        return remainingTimeInMs > 0
    }

    protected validateGameSDKTournamentPayload(payload: GameSDKTournamentPayload): boolean {
        if (!V.isString(payload.playerId) || payload.playerId === '') {
            return false
        }

        if (!V.isString(payload.playerName) || payload.playerName === '') {
            return false
        }

        if (!V.isString(payload.leaderboardId) || payload.leaderboardId === '') {
            return false
        }

        return true
    }

    protected parseTournamentPayload(payload: unknown) {
        if (typeof payload !== 'string') {
            return {}
        }

        try {
            const parsedPayload = JSON.parse(payload)
            return parsedPayload
        } catch {
            return {}
        }
    }
}

class TournamentUtils extends TournamentUtilsCore {
    // ? Fetch and return all tournament data
    public static async requestTournamentDataAsync(): Promise<TournamentData[]> {
        const instance = this.inst as TournamentUtils

        await Promise.all([instance.fetchTournamentsAsync$(), instance.fetchGlobalTournamentLeaderboardAsync$()])

        const playerTournamentData = instance.processPlayerTournamentData()
        const globalTournamentData = instance.processGlobalTournamentLeaderboardData()

        playerTournamentData.sort((a, b) => a.endTime - b.endTime)
        globalTournamentData.sort((a, b) => a.endTime - b.endTime)

        instance.tournamentData$ = [...globalTournamentData, ...playerTournamentData]

        for (const tournament of instance.tournamentData$) {
            const { payload } = tournament
            const { leaderboardId } = payload

            if (!leaderboardId) continue
            if (leaderboard.isExistLeaderboardId(leaderboardId)) continue

            leaderboard.addLeaderboard({
                type: 'tournament',
                name: leaderboardId,
                autoSortRank: true,
                leaderboardId,
            })
        }

        return instance.tournamentData$
    }

    public static saveTournamentProgress(level: number, score: number, tournamentId: string): void {
        const { tournamentProgress } = player.getGameData() ?? {}
        const previousTournamentProgress = tournamentProgress?.[tournamentId]

        const newLevel = Math.max(level, previousTournamentProgress?.level ?? 0)
        const newScore = Math.max(score, previousTournamentProgress?.score ?? 0)
        const replayCount = previousTournamentProgress?.replayCount ?? 0

        const newTournamentProgress = {
            level: newLevel,
            score: newScore,
            replayCount,
        }

        player.setGameData({
            tournamentProgress: {
                ...tournamentProgress,
                [tournamentId]: newTournamentProgress,
            },
        })
    }

    public static syncTournamentProgress(): void {
        const { tournamentProgress } = player.getGameData() ?? {}
        if (!tournamentProgress) return

        const instance = this.inst as TournamentUtils

        for (const tournamentId in tournamentProgress) {
            const tournament = A.search(instance.tournamentData$, (tournament) => {
                return tournament.id === tournamentId
            })

            if (!tournament) delete tournamentProgress[tournamentId]
        }

        player.setGameData({ tournamentProgress })
    }

    protected processPlayerTournamentData(): TournamentData[] {
        const tournaments: TournamentData[] = []

        for (const tournament of this.playerTournaments$) {
            const tournamentId = `${tournament.getID()}`
            const contextId = tournament.getContextID()
            const endTime = tournament.getEndTime()
            const payload = this.parseTournamentPayload(tournament.getPayload())

            if (!this.validateGameSDKTournamentPayload(payload)) {
                continue
            }

            const isExpiredTournament = endTime < Date.now() / 1000

            if (isExpiredTournament) continue

            const tournamentType = tournament.getTournamentType()

            if (tournamentType === 'PAGE_HOSTED') continue

            const playerName = payload.playerName ?? 'Player'

            const tournamentData = {
                id: tournamentId,
                title: tournament.getTitle() ?? this.createDefaultTournamentName(playerName),
                endTime,
                contextId,
                payload,
                tournamentType,
            } as TournamentData

            tournaments.push(tournamentData)
        }

        return tournaments
    }

    protected processGlobalTournamentLeaderboardData(): TournamentData[] {
        const tournaments: TournamentData[] = []

        const { Enabled: LeaderboardMockEnabled } = LeaderboardsMockupConfig
        if (LeaderboardMockEnabled) {
            for (const tournament of this.playerTournaments$) {
                const tournamentType = tournament.getTournamentType()
                if (tournamentType !== 'PAGE_HOSTED') continue

                const tournamentId = `${tournament.getID()}`
                const contextId = tournament.getContextID()
                const endTime = tournament.getEndTime()
                const payload = this.parseTournamentPayload(tournament.getPayload())

                const tournamentData = {
                    id: tournamentId,
                    title: tournament.getTitle() ?? 'Global Tournament',
                    endTime,
                    contextId,
                    payload,
                    tournamentType,
                }

                tournaments.push(tournamentData)
            }

            return tournaments
        }

        for (const data of this.globalTournamentLBData$) {
            let description: GlobalTournamentLBPayload

            try {
                description = JSON.parse(data.description)
            } catch {
                continue
            }

            if (!this.validateExpireTime(data)) continue

            if (!this.validateGlobalTournamentPayload(description)) continue

            // ? In restricted mode, only show restricted tournaments
            const isNEZP = 'isNEZP' in GameSDK.extra && GameSDK.extra.isNEZP()
            const isRestricted = 'isRestricted' in GameSDK.extra && GameSDK.extra.isRestricted()

            if (isNEZP || isRestricted) {
                if (!description.restrict) continue
            } else {
                if (description.restrict) continue
            }

            const title = description.i18n?.[language.getCurrentLocale()]?.title ?? data.name

            const tournament = {
                id: description.tournamentId,
                title,
                endTime: this.getTournamentEndTime(data),
                contextId: description.contextId,
                payload: {
                    // ? Global tournaments don't have player data
                    playerId: 'global',
                    playerName: 'global',
                    leaderboardId: data._id,
                    themeId: description.themeId ?? ThemeKeys.SUMMER,
                },
                tournamentType: 'PAGE_HOSTED',
            } as TournamentData

            tournaments.push(tournament)
        }

        return tournaments
    }

    protected createDefaultTournamentName(name: string): string {
        const shortName = name.substring(0, 7)
        const tournamentName = `${shortName.split(' ')[0] || shortName}'s Tournament`
        const normalizedName = S.removeDiacritics(tournamentName)
        return `${normalizedName}`
    }
}

export default TournamentUtils
