import { loadWasmModuleBox2D, loadWasmModuleBullet, loadWasmModulePhysX, loadWasmModuleSpine } from 'cc'

type WasmModuleName = 'spine' | 'box2d' | 'physx' | 'bullet'

const {
    Utils: { Function: F },
} = Ludex

class WasmUtilsCore {
    protected static inst$: WasmUtilsCore

    protected mutex$ = F.mutex()

    public static get inst() {
        if (!this.inst$) {
            this.inst$ = new this()
        }

        return this.inst$
    }

    public static async loadModuleAsync(name: WasmModuleName): Promise<void> {
        const lock = await this.inst.mutex$.use(`module-wasm-${name}`)

        try {
            switch (name) {
                case 'spine':
                    if (typeof loadWasmModuleSpine !== 'function') return

                    await loadWasmModuleSpine()
                    break
                case 'box2d':
                    if (typeof loadWasmModuleBox2D !== 'function') return

                    await loadWasmModuleBox2D()
                    break
                case 'physx':
                    if (typeof loadWasmModulePhysX !== 'function') return

                    await loadWasmModulePhysX()
                    break
                case 'bullet':
                    if (typeof loadWasmModuleBullet !== 'function') return

                    await loadWasmModuleBullet()
                    break
            }
        } finally {
            lock.release()
        }
    }
}

class WasmUtils extends WasmUtilsCore {
    // * Custom logic here
}

export default WasmUtils
