type ValueOf<T> = T[keyof T]
type TResourcePathValue = ValueOf<typeof ResourcePaths>

const values = O.vals(ResourcePaths) as TResourcePathValue[]
export const ResourcePathsEnum = values.reduce(
    (result, key, index) => {
        result[key] = index
        return result
    },
    {} as Record<TResourcePathValue, number>,
)

ccenum(ResourcePathsEnum)

export default ResourcePaths
export const ResourcePathsMapping = values
